.search-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 50px auto;
    max-width: 500px;
  }
  
.inputsearch {
    padding: 15px;
    border: none;
    border-radius: 25px;
    font-size: 20px;
    box-shadow: 4px 6px 0px 3px rgb(131 178 219 / 40%);
    flex-grow: 1;
    background-color: #f2f2f2;
    color: #333;
  }
  
  .search-button {
    background-color: #333;
    border: none;
    cursor: pointer;
    margin-left: 10px;
    padding: 15px;
    border-radius: 25px;
  }
  
  .search-button svg {
    fill: #fff;
    height: 30px;
    width: 30px;
    transition: transform 0.2s ease-in-out;
  }
  
  .search-button:hover svg {
    transform: scale(1.1);
  }
  
  .search-button:focus {
    outline: none;
  }
  
  .search-button:active svg {
    transform: scale(0.9);
  }
  @media screen and (max-width: 500px) {
  .inputsearch {
      padding: 10px;
      font-size: 16px;
    }
    .search-button {
      padding: 10px;
    }
  }  
  .cardPro {
    overflow: visible;
    width: 100%;
    height: 300px;
  }
  .titel{
    position: absolute;
  }
  .imgpro{
    max-width: 300px;
  }
  .commentPro{
    border-radius: 5px;
    border: none;
    background: #dcdcdcc4;
    color: #171718;
    text-align: center;
  }
  .content {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: transform 300ms;
    box-shadow: 0px 0px 10px 1px #000000ee;
    border-radius: 5px;
  }
  
  .front, .back {
    background-color: #151515;
    position: absolute;
    width: 99%;
    height: 99%;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    border-radius: 5px;
    overflow: hidden;
  }
  
  .back {
    width: 100%;
    height: 100%;
    justify-content: center;
    display: flex;
    align-items: center;
    overflow: hidden;
  }
  
  .back::before {
    position: absolute;
    content: ' ';
    display: block;
    width: 160px;
    height: 160%;
    background:  linear-gradient(45deg, transparent, #66bfff, #f33434, #ed4747, #6b66ff, transparent);
    animation: rotation_481 5000ms infinite linear;
  }

  @keyframes rotation_481 {
    0% {
      transform: rotateZ(0deg);
    }
  
    0% {
      transform: rotateZ(360deg);
    }
  }
  .front {
    color: white;
  }
  .front .front-content {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .front-content .badge {
    background-color: #00000055;
    padding: 9px;
    border-radius: 10px;
    backdrop-filter: blur(2px);
    width: fit-content;
  }
  .description {
    box-shadow: 0px 0px 10px 5px #00000088;
    width: 100%;
    padding: 10px;
    background-color: #00000099;
    backdrop-filter: blur(5px);
    border-radius: 5px;
  }
  .title {
    font-size: 11px;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .title p {
    width: 50%;
  }
  .cardPro-footer {
    color: #ffffff88;
    margin-top: 5px;
    font-size: 17px;
  }
  .front .img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    display: flex;
    align-items: center;
    opacity: 0.9;
  }

