  /*/////////////////*/
.tag-list {
  width: 100%;
  max-width: 90vw;
  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  gap: 1rem 0;
  position: relative;
  padding: 1.5rem 0;
  overflow: hidden;
  margin: auto;
}
.posts{
  width: 80%;
}

#fixbtn{
  display: none;
  position: fixed;
  top: 30px;
  left: 0px;
  background: #2a2b38;
  z-index: 19;
  padding-top: 20px;
}
  #proType {
    animation :open 500ms forwards linear 1;
  }


.carousel-item.activePost {
  display: block;
}

.loop-slider{
  animation-timing-function: linear;
  animation-iteration-count: 1;
  animation-name: back;
  animation-duration: 2s;
}
.loop-slider .inner {
  display: flex;
  width: fit-content;
  animation-timing-function: linear;
  animation-play-state:running;
  animation-iteration-count: infinite;
  animation-name: loop;
  animation-duration: var(--duration);
  animation-direction:var(--direction) ;

}
.loop-slider .inner:hover {
  animation-play-state:paused;
}
.tag {
  display: flex;
  align-items: center;
  gap: 0 0.2rem;
  color: #e2e8f0;
  font-size: 0.9rem;
  background-color: #334155;
  border-radius: 0.4rem;
  margin-right: 1rem;
}
.tag button {
  font-size: 1rem;
  color: #64748b;
}
.tag button:hover{
  color:#4762e1;
}
@keyframes back {
  0%{
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes open {
  from { transform: translateX(-120%); }
  to { transform: translateX(0%); }
}
@keyframes close {
  from { transform: translateX(0%); }
  to { transform: translateX(-120%); }
}
